import React from "react";
import { Span, Button } from "./style.js";
import { selectTasks, toggleHideDone, setAllDone, selectHideDone } from "../tasksSlice";
import { useDispatch, useSelector } from "react-redux";


const Buttons = () => {
    const dispatch = useDispatch();

    const tasks = useSelector(selectTasks);
    const hideDone = useSelector(selectHideDone);

    if (tasks.length === 0) {
        return null;
    }

    return (
        <Span>
            <Button onClick={() => dispatch(toggleHideDone())}>{hideDone ? "Poka≈º uko≈Ñczone" : "Ukryj uko≈Ñczone"}</Button>
            <Button onClick={() => dispatch(setAllDone())} disabled={tasks.tasks.every(({ done }) => done)}>Uko≈Ñcz wszystkie</Button>
        </Span >
    )
}

export default Buttons;